.call-to-act {
    position: relative;
}
.call-to-act__box {
  padding: 70px 290px 70px 164px;
  box-shadow: 50px 130px 100px 0px rgba(0, 0, 0, 0.15);
  background: rgb(250, 250, 250);
  position: absolute;
  top: -172px;
  left: 0;
}
.call-to-act__title {
  color: $add-green;
  font-size: 60px;
  font-weight: 700;
  line-height: 74px;
  letter-spacing: 0px;
  text-align: left;
}
.call-to-act__link {
  font-family: FontAwesome;
  margin-top: 50px;
  color: $add-gray;
  font-size: 16px;
  font-weight: 700;
  line-height: 0px;
  letter-spacing: 0px;
  text-align: left;
  text-decoration-line: underline;
  display: flex;
  align-items: center;
}